import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import firebase from "firebase"
import { AppService } from 'src/app/app.service';
@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.scss']
})
export class SignUpComponent implements OnInit {
  loginForm: FormGroup;
  constructor(private app: AppService) { }
  ngOnInit() {
    this.loginForm = new FormGroup({
      name: new FormControl("", Validators.required),
      email: new FormControl("", Validators.required),
      password: new FormControl("", Validators.required),
    })
  }
  singup() {
    firebase.app("auth").auth().createUserWithEmailAndPassword(this.loginForm.value.email, this.loginForm.value.password).then(res => {
      firebase.app("auth").database().ref().push({
        id: res.user.uid,
        email: res.user.email,
        name: this.loginForm.value.name,
        picture: "assets/img/user.svg",
      })
      this.app.loginWindow = false;
    }).catch(err => {
      console.log(err)
    })
  }
}
